.root {
  padding-right: 10px;
  color: #414042; }

.highlight {
  margin-right: 8px;
  margin-left: 8px;
  border-radius: 3px;
  background: rgba(0, 0, 0, 0.05);
  color: #fff; }

.highlight:hover {
  background: rgba(0, 0, 0, 0.1); }

.spacer {
  color: rgba(50, 50, 50, 0.3); }

.link {
  cursor: pointer;
  text-decoration: none;
  color: #414042; }

.link:visited {
  text-decoration: none;
  color: #414042; }

.link:hover {
  text-decoration: underline;
  color: #414042; }



