.root {
  background-color: #fbfbfb;
  color: #414042;
  height: 100vh; }

.container {
  margin: 0 auto;
  margin-top: 20px;
  width: 930px;
  border-color: #e5e6e6;
  border-width: 1px;
  border-style: solid;
  background-color: white;
  border-radius: 2px; }

.rowContainer {
  cursor: pointer;
  border-radius: 2px;
  padding: 20px;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-radius: 2px;
  border-bottom-color: #e5e6e6;
  display: -webkit-box;
  display: -webkit-flex;
  display: -webkit-flexbox;
  display: -moz-flex;
  display: -moz-box;
  display: -ms-flexbox;
  /* NEW - Chrome */
  display: flex;
  flex-direction: row;
  -webkit-flex-direction: row; }

.rowContainer:hover {
  background-color: #f1f1f2; }

.rowContainerKey {
  border-radius: 2px;
  padding: 10px 20px 10px 20px;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-radius: 2px;
  border-bottom-color: #e5e6e6;
  display: -webkit-box;
  display: -webkit-flex;
  display: -webkit-flexbox;
  display: -moz-flex;
  display: -moz-box;
  display: -ms-flexbox;
  /* NEW - Chrome */
  display: flex;
  flex-direction: row;
  -webkit-flex-direction: row;
  align-items: flex-end; }

.tilteDiv {
  display: -webkit-box;
  display: -webkit-flex;
  display: -webkit-flexbox;
  display: -moz-flex;
  display: -moz-box;
  display: -ms-flexbox;
  /* NEW - Chrome */
  display: flex;
  flex-direction: row;
  flex: 1; }

.title {
  font-weight: bold;
  font-size: 1.25em; }

.name {
  flex: 1; }

.nameKey {
  font-size: 1.25em;
  font-weight: bold;
  flex: 1; }

.rolling {
  flex: 0.25; }

.rolligKey {
  flex: 0.25;
  font-size: 0.75em;
  font-weight: 600;
  flex: 1; }

.party {
  flex: 0.5; }

.partyKey {
  flex: 0.5;
  font-size: 0.75em;
  font-weight: 600; }

.winners {
  flex: 0.2; }

.winnersKey {
  flex: 0.2;
  font-size: 0.75em;
  font-weight: 600; }

.prize {
  flex: 0.34; }

.prizeKey {
  flex: 0.34;
  font-size: 0.75em;
  font-weight: 600; }

.entry {
  flex: 0.4; }

.entryKey {
  flex: 0.4;
  font-size: 0.75em;
  font-weight: 600; }

.size {
  flex: 0.15;
  text-align: right; }

.sizeKey {
  flex: 0.15;
  font-size: 0.75em;
  font-weight: 600;
  text-align: right; }



