.container {
  display: -webkit-box;
  display: -moz-box;
  /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;
  /* TWEENER - IE 10 */
  display: -webkit-flex;
  /* NEW - Chrome */
  display: -webkit-box;
  display: flex;
  flex-direction: row;
  flex: 1;
  border-bottom-color: #e5e6e6;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  padding: 5px;
  background-color: #fff;
  color: #414042; }

.candidate {
  flex: 3;
  align-self: center; }

.candidateText {
  font-weight: 600; }

.place {
  flex: 1;
  align-self: center;
  margin-left: 20px; }

.placeText {
  font-weight: 600;
  padding-left: 10px; }

.state {
  flex: 2;
  align-self: center;
  margin-left: 20px; }

.stateText {
  font-weight: 600;
  padding-left: 10px; }

.rightSide {
  flex: 4;
  display: -webkit-box;
  /* OLD - iOS 6-, Safari 3.1-6 */
  display: -moz-box;
  /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;
  /* TWEENER - IE 10 */
  display: -webkit-flex;
  /* NEW - Chrome */
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding-right: 5px;
  align-items: center; }

.clearDiv {
  border-radius: 2px;
  margin-left: 20px;
  height: 40px;
  width: 40px;
  align-items: center;
  cursor: pointer;
  display: -webkit-box;
  /* OLD - iOS 6-, Safari 3.1-6 */
  display: -moz-box;
  /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;
  /* TWEENER - IE 10 */
  display: -webkit-flex;
  /* NEW - Chrome */
  display: flex;
  background-size: 40px 40px;
  background-image: url("../../static/photos/xgray40x40@2x.png"); }

.clearDiv:hover {
  background-image: url("../../static/photos/xwhite40x40@2x.png"); }

.clearPicture {
  width: 40px;
  height: 40px; }

.clearText {
  padding: 10px;
  font-size: 0.75em; }



