.root {
  background-color: #fbfbfb; }

.container {
  width: 950px;
  margin: 0 auto;
  display: -webkit-box;
  /* OLD - iOS 6-, Safari 3.1-6 */
  display: -moz-box;
  /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;
  /* TWEENER - IE 10 */
  display: -webkit-flex;
  /* NEW - Chrome */
  display: flex;
  flex-direction: row;
  min-height: 100vh;
  padding-bottom: 300px; }

.candidates {
  flex: 0.55; }

.states {
  flex: 2;
  padding-left: 10px; }

.candidateInfoText {
  font-size: .75em;
  padding-left: 10px;
  font-weight: 600; }

.stateInfoText {
  font-size: .75em;
  font-weight: 600; }

.selectBoxContainer {
  display: -webkit-box;
  /* OLD - iOS 6-, Safari 3.1-6 */
  display: -moz-box;
  /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;
  /* TWEENER - IE 10 */
  display: -webkit-flex;
  /* NEW - Chrome */
  display: flex;
  flex-direction: row;
  flex: 1; }

.chain {
  display: -webkit-box;
  /* OLD - iOS 6-, Safari 3.1-6 */
  display: -moz-box;
  /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;
  /* TWEENER - IE 10 */
  display: -webkit-flex;
  /* NEW - Chrome */
  display: flex;
  flex-direction: column;
  flex: 1;
  margin-top: 20px;
  border-radius: 2px;
  border-style: solid;
  border-width: 1px;
  border-color: #e5e6e6;
  margin-right: 10px;
  border-bottom-width: 1px; }

.rightOfSelectBox {
  display: -webkit-box;
  /* OLD - iOS 6-, Safari 3.1-6 */
  display: -moz-box;
  /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;
  /* TWEENER - IE 10 */
  display: -webkit-flex;
  /* NEW - Chrome */
  display: flex;
  flex-direction: row;
  flex: 0.7;
  background-color: #fbfbfb;
  align-items: center;
  padding-left: 20px;
  justify-content: flex-end;
  margin-right: 10px;
  padding-right: 10px; }

.pointInfo {
  font-weight: 600;
  padding: 20px; }

.spacer {
  flex: 1;
  height: 400px; }

.clearDiv {
  padding: 10px;
  background-color: #f1f1f2;
  border-radius: 2px;
  margin-left: 20px; }

.clearText {
  padding: 10px;
  font-size: 0.75em;
  color: white; }



